    height: 448px;
    color: white;
    
}
.content{
    padding-top: 140px;
    padding-left: 15px;
    height: 190px;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.buttons{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
}
.buttons:hover{
    color: black;
    background-color: #e6e6e6;
}
